<template>
  <v-app>
    <span class="bg" />
    <v-content>
      <v-container
        class="fill-height"
        fluid
      >
        <v-row
          justify="center"
        >
          <v-col
            cols="12"
            sm="8"
            md="5"
          >
            <v-card
              elevation="5"
            >
              <v-row>
                <v-col
                  cols="12"
                >
                  <v-img
                    max-height="200"
                    src="@/assets/logo.png"
                  />
                  <!-- <v-card-title>
                    <h1 class="text-center display-2 mb-4">
                      Ficha de registro clínico
                    </h1>
                  </v-card-title> -->
                  <p class="text-center">
                    Inicia seción con tus credencales
                  </p>
                  <v-card-text>
                    <v-form
                      ref="form1"
                      v-model="valid1"
                      lazy-validation
                    >
                      <v-text-field
                        v-model="usuario.username"
                        label="Usuario"
                        filled
                        rounded
                        dense
                        name="username"
                        prepend-icon="mdi-account"
                        type="text"
                        color="teal accent-3"
                        :rules="[v => !!v || 'El nombre de usuario es requerido',
                        ]"
                      />
                      <v-text-field
                        id="password"
                        v-model="usuario.password"
                        filled
                        rounded
                        dense
                        label="Password"
                        name="password"
                        prepend-icon="mdi-lock"
                        :type="show ? 'text' : 'password'"
                        color="teal accent-3"
                        :rules="[v => !!v || 'La contraseña es requerido',
                                 v => (v || '').length >= 8 || 'Mínimo debe ser de 8 caracteres',
                        ]"
                        :append-icon="show ? 'mdi-eye-off' : 'mdi-eye'"
                        @click:append="show = !show"
                      />
                    </v-form>
                  </v-card-text>
                  <div class="text-center mt-3">
                    <v-btn
                      rounded
                      color="secondary"
                      type="submit"
                      @click="login()"
                    >
                      Iniciar sesión
                    </v-btn>
                  </div>
                </v-col>
              </v-row>
            </v-card>
          </v-col>
        </v-row>
        <base-alert />
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
  import { mapState, mapMutations } from 'vuex'
  export default {
    data () {
      return {
        step: 1,
        usuarioRegister: {
          email: '',
          rolId: '',
          username: '',
          password: '',
          confirmarPassword: '',
        },
        usuario: {
          username: '',
          password: '',
        },
        valid: false,
        valid1: false,
        show: false,
        roles: [
          { text: 'Comprador', value: 1 },
          { text: 'Desarrollador', value: 2 },
        ],
        usernameRules: [v => !!v || 'El username es requerido'],
        apellidoRules: [v => !!v || 'El apellido es requerido'],
        emailRules: [
          v => !!v || 'El correo es requerido',
          v => /.+@.+/.test(v) || 'El correo no es valido',
        ],
        rules: {
          required: v => !!v || 'La contraseña es requerida',
          min: v => (v || '').length >= 8 || 'Mínimo debe ser de 8 caracteres',
        },
      }
    },
    computed: {
      ...mapState(['credenciales']),
      confirmacionPassword () {
        return (
          this.usuarioRegister.password === this.usuarioRegister.confirmarPassword ||
          'La contraseña no coinciden'
        )
      },
    },
    methods: {
      ...mapMutations(['alert', 'LOGIN']),
      resetForm1 () {
        this.step++
        this.$refs.from1.reset()
      },
      resetForm () {
        this.step--
        this.$refs.from.reset()
      },
      login () {
        var username = this.usuario.username === this.credenciales.username
        var password = this.usuario.password === this.credenciales.password
        if (this.$refs.form1.validate() && username && password) {
          this.LOGIN(true)
        } else {
          this.alert({
            color: 'error',
            text: 'Estas credenciales no coinciden con las registradas',
          })
        }
      },
    },
  }
</script>
<style scoped>
  .bg {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background: url( '../assets/fondoMosaico.jpg');
    background-repeat: repeat;
    background-position: 0 0;
  }
</style>
